
 

const popupGet = document.querySelector('#popup-get');

const setTemplateFrame = () => {
    const payForm = document.querySelector('#omForm-root');
    const content = popupGet.querySelector('.modal__content');
    

    content.insertAdjacentElement('beforeend', payForm) 
}


document.addEventListener("DOMContentLoaded", function() {
    setTemplateFrame()
 })